.select-list-page {
    background-color: rgb(36, 36, 36);
    border: 2px solid white;
    color: white;
    width: 100%;
    border-radius: 10px;
    display: flex;
    justify-content: space-evenly;
    padding: 20px;
    padding-top: 40px;
    box-sizing: border-box;
}

.select-list-page p {
    text-transform: uppercase;
    font-size: 40px;
    font-weight: 500;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
    color: rgba(0, 0, 0, 0.6);
    text-shadow: 2px 2px 3px rgba(255, 255, 255, 0.1);
}

.select-list-page__title {
    color: white;
    text-transform: uppercase;
    margin: 0;
    padding: 0;
    margin-bottom: 20px;
}

.select-list-page__item {
    border-top: 2px solid rgb(80, 80, 80);
    font-size: 22px;
    padding: 14px;
}

.select-list-page__list {
    /* margin: 0 auto; */
}

.select-list__planet__img {
    width: 220px;
    border-radius: 10px;
    /* margin: 10px; */
    object-fit: contain;
    object-position: top;
}

.persone_img {
    width: 100%;
    object-fit: contain;
}

.persone_img-flex {
    flex-wrap: wrap;
}

@media only screen and (max-width: 680px) {
    .select-list-page {
        flex-wrap: wrap;
        padding: 5px;
    }
    .select-list__planet__img {
        border-radius: 10px;
        width: 100%;
        margin-bottom: 20px;
        margin-top: 20px;
    }
    .select-list-page__item {
        width: 90%;
    }
    .select-list-page__title {
        text-align: center;
    }
    .select-list-page__list {
        margin: 0;
        width: 100%;
    }
    .select-list-page p {
        font-size: 26px;
    }
}

@media only screen and (max-width: 1080px) {
    .select-list__planet__img {
        width: 150px;
        margin-right: 20px;
    }
}