.page-main {
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 44px;
    margin-top: 40px;
    width: 100%;
}

@media only screen and (max-width: 800px) {
    .page-main {
        display: block;
        width: 100%;
        grid-template-columns: 1fr 2fr;
    }
}