.select-list {
    background-color: rgb(36, 36, 36);
    border: 2px solid white;
    color: white;
    width: 100%;
    list-style: none;
    margin: 0;
    padding: 0;
    border-radius: 10px;
    margin-right: 40px;
}

.select-item:hover {
    background-color: rgb(80, 80, 80);
}

.select-item {
    border-top: 2px solid rgb(80, 80, 80);
    font-size: 24px;
    padding: 14px;
    height: 40px;
}


/*  */

@media only screen and (max-width: 800px) {
    .select-list {
        width: 100%;
        text-align: center;
        margin-bottom: 40px;
    }
}