.header__logo {
    width: 150px;
    margin: 0;
    padding: 0;
}

.header__logo:hover {
    -webkit-box-shadow: 0px 0px 54px -12px rgba(255, 56, 0, 0.41);
    -moz-box-shadow: 0px 0px 54px -12px rgba(255, 56, 0, 0.41);
    box-shadow: 0px 0px 54px -12px rgba(255, 56, 0, 0.41);
}

.header__item {
    text-transform: uppercase;
    background-color: rgb(36, 36, 36);
    padding: 10px 20px;
    margin: 10px;
    border-radius: 10px;
    border: 2px solid white;
}

.header__item a {
    text-decoration: none;
    color: rgb(130, 199, 255);
}

.header__item:hover {
    background-color: rgb(120, 120, 120, 0.6);
}

.header__list {
    list-style: none;
    display: flex;
}

.header {
    /* width: 80%; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0;
}

@media only screen and (max-width: 680px) {
    .header {
        width: 100%;
        flex-wrap: wrap;
        text-align: center;
        justify-content: center;
    }
    .header__list {
        flex-wrap: wrap;
        padding-left: 10px;
        padding-right: 10px;
    }
    .header__item {
        width: 100%;
    }
    .header__logo {
        padding: 0;
    }
}