.planet {
    display: flex;
    justify-content: flex-start;
}

.planet-block {
    position: relative;
    background-color: rgb(36, 36, 36);
    border: 2px solid white;
    color: white;
    border-radius: 10px;
    display: flex;
    padding: 40px;
    margin: 0;
    justify-content: space-between;
    align-items: center;
}

.planet__img {
    width: 240px;
    height: 240px;
    border-radius: 10px;
    object-fit: contain;
}

.planet__title {
    color: white;
    text-transform: uppercase;
    font-size: 30px;
    margin: 0;
    padding: 0;
    padding-bottom: 10px;
}

.planet__info {
    list-style: none;
    margin: 0;
    padding: 0;
}

.planet__item {
    border-top: 2px solid rgb(80, 80, 80);
    font-size: 24px;
    padding: 14px;
}

.planet__list {
    margin-left: 20px;
}

@media only screen and (max-width: 680px) {
    .planet {
        flex-wrap: wrap;
        padding: 5px;
    }
    .planet__img {
        border-radius: 10px;
        width: 300px;
        margin: 20px auto;
    }
    .planet__item {
        width: 90%;
    }
    .planet__title {
        text-align: center;
        padding-top: 0;
    }
    .planet__list {
        margin: 0;
        width: 100%;
    }
}

.cloneMan {
    width: 228px;
    padding-right: 50px;
}

@media only screen and (max-width: 1075px) {
    .cloneMan {
        display: none;
    }
}